<script setup>
    import { ref } from 'vue';
    import { useStore } from 'vuex';
    import post from '../../utils/post';

    const selectedIndex = ref(0);
    const store = useStore();

    function changeLocation(side){
        if(side === 'left'){
            if(selectedIndex.value > 0) {
                selectedIndex.value -= 1;
                post("Chosen", { index: Math.abs(selectedIndex.value + 1) });
                return;
            }
        } else if(side === 'right'){
            if(selectedIndex.value < Object.values(store.state.locations).length - 1) {
                selectedIndex.value += 1;
                post("Chosen", { index: Math.abs(selectedIndex.value + 1) });
                return;
            }
        }

    }

    function selectLocation(){
        post("Spawn", {index: Math.abs(selectedIndex.value + 1)});
    }
</script>

<template>
    <div id="location">
        <div id="container-location">
            <div class="background-one-location"></div>
            <div class="content-container-location">
                <div class="arrow-character-location" @click="changeLocation('left')" :class="{'disabled-arrow': selectedIndex === 0}"></div>
                <div class="arrow-character-location" @click="changeLocation('right')" :class="{'disabled-arrow': selectedIndex === Object.values(store.state.locations).length - 1}"></div>
                <div class="title-container-location">
                    <div class="title-frame-location">
                        <p>SELEÇÃO</p>
                        <p>DE LOCALIZAÇÃO</p>
                    </div>
                    <div class="bar-frame-location">
                        <div class="icon-bar-frame-location">
                            <img src="./images/location.svg" />
                        </div>
                        <p>{{store.state.locations[selectedIndex]?.name}}</p>
                    </div>
                </div>
                <button class="button-location" @click="selectLocation">SELECIONAR</button>
            </div>
        </div>
    </div>
</template>

<style scoped>
     #location {
        width: 100vw;
        height: 100vh;
    }

    #container-location {
        width: 59.32291666666667vw;
        height: 100%;
        background: linear-gradient(270deg, rgba(0, 0, 0, 0) 8.52%, rgba(0, 0, 0, 0.58) 100%);
    }

    .background-one-location {
        width: 100%;
        height: 100%;
        background-image: url('./images/eclipse.png');
        background-size: 100% 100%;
        position: absolute;
        z-index: 1;
    }

    .content-container-location {
        position: absolute;
        z-index: 2;
        padding: 0 8.229166666666668vw;
        transform: translate(0, -50%);
        top: 50%;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    .arrow-character-location {
        width: 3.125vw;
        height: 3.125vw;
        cursor: pointer;
        transition: all ease .2s;
        background-size: 100% 100%;
    }

    .arrow-character-location:nth-child(1) {
        background-image: url('./images/arrow-left.svg');
    }

    .arrow-character-location:nth-child(1):not(.disabled-arrow):hover {
        background-image: url('./images/arrow-left-active.svg');
    }

    .arrow-character-location:nth-child(1):active {
        background-image: url('./images/arrow-left.svg');
    }

    .arrow-character-location:nth-child(2) {
        background-image: url('./images/arrow-right.svg');
    }

    .arrow-character-location:nth-child(2):not(.disabled-arrow):hover {
        background-image: url('./images/arrow-right-active.svg');
    }

    .arrow-character-location:nth-child(2):active {
        background-image: url('./images/arrow-right.svg');
    }

    .title-container-location {
        position: absolute;
        bottom: -10.364583333333334vw;
        left: 0;
        transform: translate(0, 100%);
        margin: 0 8.229166666666668vw;
    }

    .title-frame-location {
        font-weight: 700;
        font-size: 2.5vw;
        color: #FFFFFF;
    }

    .title-frame-location p:nth-child(2) {
        margin-top: -1vw;
    }

    .bar-frame-location {
        width: 22.65625vw;
        height: 3.4895833333333335vw;
        background: rgba(0, 0, 0, 0.58);
        border-radius: 0.36458333333333337vw;
        margin-top: 2.03125vw;
        display: flex;
        align-items: center;
        font-size: 1.25vw;
        font-weight: 500;
        color: #FFF;
    }

    .icon-bar-frame-location {
        width: 3.854166666666667vw;
        height: 100%;
        background: linear-gradient(92.51deg, rgba(0, 188, 126, 0.1) -5.33%, rgba(0, 157, 105, 0.1) 100.68%);
        border-radius: 0.36458333333333337vw;
        margin-right: 1.3020833333333335vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .icon-bar-frame-location img {
        height: 1.25vw;
    }

    .button-location {
        position: absolute;
        bottom: -16.5625vw;
        right: 0;
        transform: translate(0, 100%);
        margin: 0 8.229166666666668vw;
        width: 22.65625vw;
        height: 4.739583333333334vw;
        background: linear-gradient(92.51deg, #00BC7E -5.33%, #009D69 100.68%);
        border-radius: 0.36458333333333337vw;
        outline: none;
        border: none;
        cursor: pointer;
        transition: all ease .4s;
        font-weight: 600;
        font-size: 1.25vw;
        color: #FFF;
    }

    .button-location:hover {
        box-shadow: 0px 0px 4.635416666666667vw rgba(0, 188, 126, 0.7);
    }

    .button-location:active {
        box-shadow: 0px 0px 0 rgba(0, 188, 126, 0.7);
    }

    .disabled-arrow {
        opacity: .2;
        cursor: default;
    }
</style>